@media screen and (max-width:1100px) {
    .fullWidthContainer {

        display: flex;
        padding: 100px;
        height: max-content;
        gap: var(--componentgap);
        flex-direction: column-reverse;
        justify-content: center;
        align-content: center;
        align-items: center;
        padding: var(--buttonpadding);
    }

    .profilePicFace {
        width: 150px;
        height: 150px;
        aspect-ratio: 1;
        border-radius: 100px;
    }

    .subDivision
{
    height: max-content;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
}
.container {
    display: flex;
    gap: 10px;
    flex-direction: column;
}
@media  screen and (min-width:1101px) {

.fullWidthContainer {
    width: 100vw;
    height: max-content;
    display: flex;
    flex-wrap: wrap;
    text-wrap: wrap;
    padding: 100px;
    gap: var(--componentgap);
    flex-direction: row;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    padding: var(--buttonpadding);
}

.fullWidthContainer p {
    text-align: justify;
    width: 75%;
    height: 100%;
    overflow-wrap: break-word;
    text-wrap: pretty;
    margin: 0;
    padding: 0.5rem;
    text-overflow: ellipsis;
    word-break: normal;
    white-space: normal;
    text-overflow: ellipsis;
    overflow: hidden;

}

.profilePicFace {
    width: 150px;
    height: 150px;
    aspect-ratio: 1;
    border-radius: 100px;
}
.subDivision
{
    height: 100%;
    width: max-content;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
}
